.book-samples {
  margin-top: 80px;
  text-align: center;
}
.book-samples h2 {
  margin-bottom: 20px;
  font-size: 24px;
  text-align: center;
}
.book-samples p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  unicode-bidi: isolate;
}
.samples-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
}
.book-sample {
  flex-basis: calc(33.333% - 40px);
  margin-bottom: 40px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 20px;
  transition: transform 0.3s ease;
}
.book-sample-light{
  background: rgba(192, 192, 192, 0.3);
}
@media only screen and (max-width: 580px) {
  .book-sample {
    flex-basis: auto;
  }
}
.book-sample:hover {
  transform: translateY(-10px);
}

.book-cover-1 {
  width: 200px;
  height: 300px;
  margin: 0 auto 20px;
  background-image: url(https://raw.githubusercontent.com/adarshagupta/trybookai/main/public-assets/paradox.png);
  background-position: center;
  background-size: 100% 100%;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  padding: 10px;
}
