.newsletter {
  margin-top: 80px;
  text-align: center;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 40px;
}
.newsletter-light{
  background: rgba(192, 192, 192, 0.3);
}
.newsletter h2 {
  margin-bottom: 20px;
  font-size: 24px;
  text-align: center;
}
.newsletter p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  unicode-bidi: isolate;
}
.newsletter form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 0em;
  unicode-bidi: isolate;
}
input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.newsletter button {
  background: var(--gradient-1);
  color: var(--text-color);
  border: none;
  padding: 10px 20px;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  transition: transform 0.3s ease;
}
