.how-it-works,
.roadmap {
  margin-top: 80px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 40px;
}
.how-it-works-light,
.roadmap-light{
  background: rgba(192, 192, 192, 0.3);
}
.how-it-works h2,
.roadmap h2 {
  margin-bottom: 20px;
  font-size: 24px;
  /* color: #ffffff; */
  text-align: center;
}
.step-container,
.roadmap-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
@media only screen and (max-width: 760px) {
  .step-container,
  .roadmap-container {
    grid-template-columns: 1fr;
  }
}
.step,
.roadmap-item {
  flex-basis: calc(50% - 20px);
  margin-bottom: 40px;
  display: flex;
  align-items: flex-start;
}
.step-icon,
.roadmap-icon {
  width: 50px;
  height: 50px;
  background: var(--gradient-1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: var(--text-color);
  margin-right: 20px;
  flex-shrink: 0;
}
.step-content,
.roadmap-content {
  flex-grow: 1;
}
.step h3,
.roadmap-item h3 {
  margin-top: 0;
}
.step p {
  display: block;
  /* margin-block-start: 1em; */
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  unicode-bidi: isolate;
}
